						cainum: 11
					},
					commentnum: 10,
					sharenum: 12
				};
				this.newslist[index].lists.push(obj);
				this.newslist[index].loadtext = "上拉加载更多";
			}, 2000);
			//如果没有数据显示
			// this.newslist[index].loadtext = "没有更多数据";
		}
